@charset "UTF-8";
/* Base
–––––––––––––––––––––––––––––––––––––––––––––––––– */
body {
  color: #22252B;
  font-family: "Lato", sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  color: #87898C;
}

/* Header
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.Header {
  background-color: white;
  border-bottom: 1px solid #EBECEE;
  box-shadow: none;
  color: #22252B;
  height: 60px;
  margin-bottom: 40px;
  justify-content: space-between;
  padding: 0 40px;
}

.Header a {
  font-size: 21px;
  font-weight: 700;
}

.Header-Utils {
  align-items: center;
  display: flex;
}

.Header-Utils a {
  margin-left: 30px;
}

.Header-Utils a svg path {
  transition: fill 0.15s ease-out;
}

.Header-Utils a:hover svg path {
  fill: #87898C;
}

/* Frame
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.Frame-body {
  height: calc(100% - 100px);
}

.Frame-panel--main {
  width: calc(100% - 320px);
}

.Frame-panel--sidebar {
  background-color: transparent;
  border-inline-end: none;
  padding: 0 0 20px;
  min-width: 320px;
}

[dir="ltr"] .Frame-panel--sidebar {
  border-right: none;
}

/* Sidebar
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.Navigation {
  padding-right: 20px;
}

.Tree-title {
  background-color: whitesmoke;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
  color: rgba(34, 37, 43, 0.54);
  font-family: "Lato", sans-serif;
  font-size: 16px;
  font-weight: bold;
  letter-spacing: 1px;
  margin-bottom: 0;
  padding: 12px 40px;
}

[dir="ltr"] .Tree-title {
  padding-left: 40px;
}

.Tree-items.Tree-depth-1 {
  padding-top: 5px;
}

.Tree-item {
  color: rgba(34, 37, 43, 0.87);
  font-family: inherit;
  font-size: 16px;
}

.Tree-entityLink {
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
  padding: 7px 40px !important;
}

.is-current .Tree-entityLink {
  background-color: whitesmoke;
}

.Navigation-group + .Navigation-group {
  margin-top: 40px;
}

#tree-components-collection-atoms,
#tree-components-collection-molecules,
#tree-components-collection-organisms,
#tree-components-collection-templates,
#tree-components-collection-pages {
  border-bottom: 1px solid #EBECEE;
  margin: 0 40px;
  padding: 10px 0 15px;
}

#tree-components-collection-atoms > h3,
#tree-components-collection-molecules > h3,
#tree-components-collection-organisms > h3,
#tree-components-collection-templates > h3,
#tree-components-collection-pages > h3 {
  color: rgba(34, 37, 43, 0.87);
  font-family: "Lato", sans-serif;
  font-size: 16px;
  letter-spacing: 1px;
  padding: 7px 0 !important;
  text-transform: uppercase;
}

#tree-components-collection-atoms > h3 span,
#tree-components-collection-molecules > h3 span,
#tree-components-collection-organisms > h3 span,
#tree-components-collection-templates > h3 span,
#tree-components-collection-pages > h3 span {
  padding-left: 40px !important;
}

#tree-components-collection-atoms > h3 span:before,
#tree-components-collection-molecules > h3 span:before,
#tree-components-collection-organisms > h3 span:before,
#tree-components-collection-templates > h3 span:before,
#tree-components-collection-pages > h3 span:before {
  height: 26px;
  left: 0 !important;
  top: 2px;
  width: 26px;
}

#tree-components-collection-atoms {
  padding-top: 5px;
}

#tree-components-collection-atoms > h3 span:before {
  background-image: url("img/icon-atoms.svg") !important;
}

#tree-components-collection-molecules > h3 span:before {
  background-image: url("img/icon-molecules.svg") !important;
}

#tree-components-collection-organisms > h3 span:before {
  background-image: url("img/icon-organisms.svg") !important;
}

#tree-components-collection-templates > h3 span:before {
  background-image: url("img/icon-templates.svg") !important;
}

#tree-components-collection-pages > h3 span:before {
  background-image: url("img/icon-pages.svg") !important;
}

.Tree-collectionItems {
  margin-left: 40px;
}

.Tree-collectionItems .Tree-entityLink {
  margin-left: -20px !important;
  padding: 7px 20px !important;
}

.Tree-collectionItems .Tree-collectionItems {
  margin-left: 20px;
}

.Tree-collectionLabel {
  padding-left: 0 !important;
}

.Tree-collectionLabel span {
  margin-left: 0 !important;
  padding-left: 0 !important;
}

.Tree-collectionLabel span:before {
  background-image: url("img/icon-navigation-opened.svg") !important;
  background-size: auto;
  left: initial !important;
  opacity: 1;
  right: 0 !important;
  top: 5px;
}

.Tree-collectionLabel:hover {
  background-color: transparent;
  color: #87898C !important;
  transition: all 0.15s ease-out;
}

.Tree-collection.is-closed .Tree-collectionLabel span:before {
  background-image: url("img/icon-navigation-closed.svg") !important;
}

/* Pen
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.Pen {
  margin: 0 20px 0 80px;
  overflow: auto;
}

.Pen-top {
  display: flex;
  margin: 0 20px 40px 0;
}

.Pen-info {
  border: none;
  margin: 0 0 0 40px;
  width: 50%;
}

.Browser {
  position: relative;
}

.Browser-controls {
  box-shadow: none;
}

.Browser-tab {
  flex: initial;
}

.Browser-tab a {
  border-bottom: 0;
  color: rgba(34, 37, 43, 0.87);
  font-family: "Lato", sans-serif;
  font-size: 16px;
  font-weight: 400;
  padding: 6px 20px;
}

.Browser-tab a:hover {
  background-color: whitesmoke;
}

.Browser-tab.is-active a {
  background-color: whitesmoke;
  font-weight: 400;
}

.Browser-panel {
  background-color: whitesmoke;
  padding: 20px;
}

.Browser-panel .Code {
  background-color: whitesmoke;
}

.Pen-header {
  border-bottom: 1px solid #EBECEE;
  justify-content: space-between;
  margin: 0 20px 40px 0;
  max-height: initial;
  padding: 0 0 10px;
}

.Pen-header .Status {
  display: inline-block;
  margin-left: 40px !important;
  padding-left: 0 !important;
  vertical-align: bottom;
}

.Pen-title {
  color: #22252B;
  font-family: "Lato", sans-serif;
  font-size: 24px;
  font-weight: 700;
}

.Pen-previewLink {
  background-color: transparent;
  border: 1px solid #87898C;
  color: #87898C;
  padding: 7px 20px;
}

.Pen-previewLink svg {
  margin-left: 10px;
  opacity: 1;
}

.Pen-previewLink:hover {
  background-color: whitesmoke;
}

.Pen-preview {
  background-color: white;
  border: none;
  margin: 0 20px 20px 0;
  overflow-x: hidden;
}

/* Prose
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.Prose {
  flex: 1 1 auto;
  font-family: "Lato", sans-serif;
  line-height: 1.625;
  max-width: initial;
  width: 50%;
}

.Prose p, .Prose > *:not(:last-child) {
  margin-bottom: 20px;
}

.Prose a {
  color: #0AD3FF;
}

.Prose a:hover {
  color: #87898C;
}

.Prose h1, .Prose h2, .Prose h3 {
  color: #22252B;
  font-family: "Lato", sans-serif;
  font-weight: 700;
}

.Prose h1 {
  border-bottom: 1px solid #EBECEE;
  font-size: 40px;
  letter-spacing: 0;
  margin-bottom: 40px !important;
  padding-bottom: 10px;
}

.Prose h2 {
  font-size: 24px;
  margin: 30px 0 20px;
}

.Prose h3 {
  font-size: 16px;
  margin: 30px 0 20px;
}

.Prose *:not(hr) + h2 {
  margin-top: 30px;
}

.Prose ul, .Prose ol, .Prose dl {
  padding-left: 20px !important;
}

.Prose dd {
  margin-bottom: 20px;
}

.Prose table {
  font-size: 16px;
  margin: 20px 0;
}

.Prose table th {
  background-color: transparent;
  border-bottom: 1px solid rgba(34, 37, 43, 0.54);
  padding: 12px 20px;
}

.Prose table tr {
  border-bottom: 1px solid #EBECEE;
}

.Prose table tr:nth-child(even) {
  background-color: transparent;
}

.Prose table td {
  padding: 12px 20px;
}

/* Status
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.Status-label {
  border: 1px solid transparent;
  border-radius: 15px;
  font-family: inherit;
  letter-spacing: .75px;
  padding: 4px 20px;
}

/* Documentation
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.Document {
  margin: 0 40px 0 80px;
  overflow: auto;
  padding: 0;
}

.Document .Prose {
  width: auto;
}

.Document-header {
  border-bottom: 1px solid #EBECEE;
  margin-bottom: 40px;
  padding-bottom: 10px;
}

.Document-title {
  color: #22252B;
  font-family: "Lato", sans-serif;
  font-size: 40px;
  font-weight: 700;
  letter-spacing: 0;
}

.overview-statuses {
  display: flex;
}

.overview-statuses .overview-status {
  width: 50%;
}

.overview-statuses .overview-status:first-child {
  margin-right: 20px;
}

.overview-statuses .overview-status:last-child {
  margin-left: 20px;
}

.swatches {
  display: flex;
  flex-wrap: wrap;
}

.swatches .color-swatch {
  border: 1px solid #EBECEE;
  border-radius: 25px;
  margin: 0 20px 20px 0;
  padding: 20px;
  width: 250px;
}

.c-white {
  color: white;
}

